import React from 'react'
import * as wsActions from '../websocketActions'
import { Button } from '@material-ui/core'

const FollowButton = props => {
    const { account, type, id } = props
    if (account && account._id && account._id !== id) {
        if (type === 'progress') {
            return account.followProgresses.includes(id) ? (
                <Button
                    variant="contained"
                    onClick={e => {
                        e.stopPropagation()
                        e.preventDefault()
                        wsActions.unfollowProgress(account._id, id)
                    }}
                >
                    Unfollow
                </Button>
            ) : (
                <Button
                    color="primary"
                    variant="contained"
                    onClick={e => {
                        e.stopPropagation()
                        e.preventDefault()
                        wsActions.followProgress(account._id, id)
                    }}
                >
                    Follow
                </Button>
            )
        } else {
            return account.followAccounts.includes(id) ? (
                <Button
                    variant="contained"
                    onClick={e => {
                        e.stopPropagation()
                        e.preventDefault()
                        wsActions.unfollowAccount(account._id, id)
                    }}
                >
                    Unfollow
                </Button>
            ) : (
                <Button
                    color="primary"
                    variant="contained"
                    onClick={e => {
                        e.stopPropagation()
                        e.preventDefault()
                        wsActions.followAccount(account._id, id)
                    }}
                >
                    Follow
                </Button>
            )
        }
    }
    return null
}

export default FollowButton
