import React, { useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

import UserButton from '../../components/UserButton/UserButton'
import Comment from 'antd/es/comment'
import Divider from 'antd/es/divider'
import moment from 'moment'
import { Card, CardContent } from '@material-ui/core'
import * as wsActions from '../../websocketActions'

moment().format()

const DashboardEvents = props => {
    const { lastNotifications } = props
    const getRewardText = item => (
        <span style={{ fontWeight: 'bold' }}>
            {item.details.reward.money
                ? '$' + item.details.reward.money
                : item.details.reward.simple || item.details.reward.itemName}
        </span>
    )

    const getStageName = stage => {
        let stageDate
        if (stage.day) {
            stageDate = moment()
                .year(stage.year)
                .dayOfYear(stage.day)
            return stageDate.format('dddd D MMM YYYY')
        } else if (stage.week) {
            stageDate = moment()
                .year(stage.year)
                .week(stage.week)
            return (
                stageDate.startOf('week').format('wo [week] D MMM YYYY - ') +
                stageDate.endOf('week').format('D MMM YYYY')
            )
        } else if (stage.month) {
            stageDate = moment()
                .year(stage.year)
                .month(stage.month)
            return stageDate.format('MMMM YYYY')
        } else if (stage.year) {
            stageDate = moment().year(stage.year)
            return stageDate.format('YYYY')
        }
    }

    useEffect(() => {
        wsActions.setLastSeenNot(
            props.account._id,
            lastNotifications.reduce(function(p, v) {
                v = parseInt(v.notId)
                return p > v ? p : v
            }, 0)
        )
    }, [lastNotifications])
    return (
        <Card>
            <CardContent>
                {lastNotifications.map((item, index) => {
                    let text = null,
                        stageName

                    switch (item.code) {
                        case 'change name':
                            text = (
                                <span>
                                    {' '}
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' changed name.'}
                                </span>
                            )
                            break
                        case 'delete account':
                            text = (
                                <span>
                                    {item.user}
                                    deleted account.
                                </span>
                            )
                            break
                        case 'friend request':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' wants to become friends with you.'}
                                </span>
                            )
                            break
                        case 'friend add':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' and '}
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.details.friend}
                                    />
                                    {' became friends.'}
                                </span>
                            )
                            break
                        case 'unfriend':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' and '}
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.details.friend}
                                    />
                                    {' stopped being friends.'}
                                </span>
                            )
                            break
                        case 'add wishlist item':
                        case 'edit wishlist item':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />{' '}
                                    {item.code === 'add wishlist item'
                                        ? 'added to wishlist a new item'
                                        : 'edited in wishlist'}{' '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/wishlist/${item.user}/${
                                                    item.details.itemId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.itemName}
                                    </span>{' '}
                                    .
                                </span>
                            )
                            break
                        case 'delete wishlist item':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' deleted '}
                                    {item.details.itemName} from wishlist.
                                </span>
                            )
                            break
                        case 'add goal':
                        case 'edit goal':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />{' '}
                                    {item.code === 'add goal'
                                        ? 'added a new goal'
                                        : 'edited goal'}{' '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${item.user}/${
                                                    item.details.itemId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.itemName}
                                    </span>
                                    {' .'}
                                </span>
                            )
                            break
                        case 'delete goal':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' deleted '}
                                    {item.details.itemName} from goals.
                                </span>
                            )
                            break
                        case 'add perk':
                        case 'edit perk':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />{' '}
                                    {item.code === 'add perk'
                                        ? 'added to shop a new item'
                                        : 'edited in shop'}{' '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/shop/${item.user}/${
                                                    item.details.itemId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.itemName}
                                    </span>
                                    {' .'}
                                </span>
                            )
                            break
                        case 'delete perk':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' deleted '}
                                    {item.details.itemName} from shop.
                                </span>
                            )
                            break
                        case 'buy perk':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' bought '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/shop/${item.user}/${
                                                    item.details.itemId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.itemName} ($
                                        {item.details.price})
                                    </span>
                                    {' from '}
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.details.owner}
                                    />
                                    {' .'}
                                </span>
                            )
                            break
                        case 'cancel transaction':
                        case 'confirm transaction':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />{' '}
                                    {item.code === 'cancel transaction'
                                        ? 'cancelled'
                                        : 'confirmed'}
                                    {' purchase of '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/shop/${item.user}/${
                                                    item.details.itemId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.itemName} ($
                                        {item.details.price})
                                    </span>
                                    {' by '}
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.details.buyer}
                                    />
                                    {' .'}
                                </span>
                            )
                            break
                        case 'start progress':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' started participating in goal '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${item.details.itemId}`
                                            )
                                        }
                                    >
                                        {item.details.itemName}
                                    </span>
                                    {' as '}
                                    {item.details.as}.
                                </span>
                            )
                            break
                        case 'stage approve':
                        case 'stage progress':
                        case 'stage fail':
                        case 'stage dismiss':
                            stageName = getStageName(item.details)
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {item.code === 'stage approve'
                                        ? ' approved  '
                                        : item.code === 'stage progress'
                                        ? ' put into process '
                                        : item.code === 'stage fail'
                                        ? ' marked as failed '
                                        : ' dismissed '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    {stageName && (
                                        <>
                                            {' for '}
                                            <span
                                                style={{ fontWeight: 'bold' }}
                                            >
                                                {stageName}
                                            </span>
                                        </>
                                    )}
                                    .
                                </span>
                            )
                            break

                        case 'stage complete':
                            text = (
                                <span>
                                    {'Progress '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    {stageName && (
                                        <>
                                            {' for '}
                                            <span
                                                style={{ fontWeight: 'bold' }}
                                            >
                                                {stageName}
                                            </span>
                                        </>
                                    )}

                                    {' was approved and finished.'}
                                </span>
                            )
                            break
                        case 'milestone finish':
                            text = (
                                <span>
                                    Milestone {item.details.itemName}
                                    {' in goal '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    {' was approved and finished.'}
                                </span>
                            )
                            break
                        case 'get reward':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' recieved '}
                                    {getRewardText(item)}
                                    {' as a reward from '}
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.details.from}
                                    />
                                    {' in goal '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    .
                                </span>
                            )
                            break
                        case 'leave progress':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' left goal '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    .
                                </span>
                            )
                            break
                        case 'edit progress':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' edited goal '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    {item.details.addedAccounts.length > 0 ? (
                                        <>
                                            {' and added '}
                                            {item.details.addedAccounts.map(
                                                ac => (
                                                    <UserButton
                                                        span={true}
                                                        noAva={true}
                                                        id={ac}
                                                    />
                                                )
                                            )}{' '}
                                        </>
                                    ) : (
                                        ''
                                    )}
                                    {item.details.droppedAccounts.length > 0 ? (
                                        <>
                                            {' and removed '}
                                            {item.details.droppedAccounts.map(
                                                ac => (
                                                    <UserButton
                                                        span={true}
                                                        noAva={true}
                                                        id={ac}
                                                    />
                                                )
                                            )}
                                            {' .'}
                                        </>
                                    ) : (
                                        '.'
                                    )}
                                </span>
                            )
                            break
                        case 'remove from progress':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' removed '}
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.details.account}
                                    />
                                    {' from goal '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    .
                                </span>
                            )
                            break
                        case 'add to progress':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' added '}
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.details.account}
                                    />
                                    {' to goal '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    .
                                </span>
                            )
                            break
                        case 'add reward':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' added '}
                                    {getRewardText(item)} as a reward for
                                    {!item.details.for ||
                                    item.details.for.length === 0
                                        ? ' all '
                                        : item.details.for.map(account => (
                                              <>
                                                  <UserButton
                                                      noAva={true}
                                                      span={true}
                                                      id={account}
                                                  />
                                                  ,
                                              </>
                                          ))}
                                    {' in goal '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    .
                                </span>
                            )
                            break
                        case 'delete reward':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {' removed '}
                                    {getRewardText(item)}
                                    {' as a reward for '}
                                    {!item.details.for ||
                                    item.details.for.length === 0
                                        ? ' all '
                                        : item.details.for.map(account => (
                                              <>
                                                  <UserButton
                                                      span={true}
                                                      noAva={true}
                                                      id={account}
                                                  />
                                                  ,
                                              </>
                                          ))}
                                    {' in goal '}
                                    <span
                                        style={{ fontWeight: 'bold' }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    .
                                </span>
                            )
                            break
                        default:
                            text = 'New event'
                            break
                    }
                    return (
                        <div key={index}>
                            {index !== 0 && (
                                <Divider style={{ margin: '5px 0px' }} />
                            )}
                            <Comment
                                avatar={
                                    <UserButton
                                        noName={true}
                                        id={item.user}
                                        span={true}
                                    />
                                }
                                content={text}
                            />
                        </div>
                    )

                    // <Card>{text}</Card>
                })}{' '}
            </CardContent>
        </Card>
    )
}
const mapStateToProps = state => {
    return {
        lastNotifications: state.lastNotifications,
    }
}

export default withRouter(connect(mapStateToProps)(DashboardEvents))
