import React, { useState } from 'react'
import { Link, withRouter } from 'react-router-dom'

import * as wsActions from '../../websocketActions'
import ActivityTable from './ActivityTable'
import EditProgress from './EditProgress'

import UserButton from '../../components/UserButton/UserButton'
import ImageCarousel from '../../components/UI/ImageCarousel/ImageCarousel'

import MasonryCard, {
    getPH,
    placeholderImages,
} from '../../components/MasonryCard'
import FollowButton from '../../components/FollowButton'
import Helmet from 'react-helmet'
import RetweetOutlined from '@ant-design/icons/RetweetOutlined'
// import Divider from 'antd/es/divider'
import ReadMore from '../../components/UI/ReadMore'
import {
    Box,
    Dialog,
    Divider,
    makeStyles,
    Paper,
    Slide,
    Typography,
    Button,
    IconButton,
    MenuItem,
    Menu,
    DialogTitle,
    DialogActions,
    DialogContent,
    DialogContentText,
    CardMedia,
    CardContent,
} from '@material-ui/core'

import { MoreVertOutlined } from '@material-ui/icons'
import ShareButton from '../../components/UI/ShareButton'
import HorizontalScrollList from '../../components/UI/HorizontalScrollList'
import Rewards from '../../components/Rewards/Rewards'

const TransitionComponent = props => <Slide direction="up" {...props} />
const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
    },
    secondaryHeading: {
        fontSize: theme.typography.pxToRem(15),
    },
    icon: {
        verticalAlign: 'bottom',
        height: 20,
        width: 20,
    },
    details: {
        alignItems: 'center',
    },
    column: {
        flexBasis: '33.33%',
    },
    helper: {
        borderLeft: `2px solid ${theme.palette.divider}`,
        padding: theme.spacing(1, 2),
    },
    link: {
        color: theme.palette.primary.main,
        textDecoration: 'none',
        '&:hover': {
            textDecoration: 'underline',
        },
    },
    title: {
        fontSize: 13,
        margin: '30px 15px 10px',
        textTransform: 'uppercase',
        fontWeight: 600,
    },
}))

const Activity = props => {
    const classes = useStyles()
    const { account, progress, state, setState, friendData } = props

    const [followersVisible, setFollowersVisible] = useState('')
    const [anchorEl, setAnchorEl] = useState(null)

    const getDay = day => {
        switch (day) {
            case '0':
                return 'Sunday'
            case '1':
                return 'Monday'
            case '2':
                return 'Tuesday'
            case '3':
                return 'Wednesday'
            case '4':
                return 'Thursday'
            case '5':
                return 'Friday'
            case '6':
                return 'Saturday'

            default:
                return ''
        }
    }

    const getRepeatType = repeat => {
        switch (repeat) {
            case 'day':
                return 'every day'
            case 'weekday':
                return 'every day of week'
            case 'week':
                return 'every week'
            case 'month':
                return 'every month'
            case 'year':
                return 'every year'
            default:
                return ''
        }
    }

    const getDropDownMeuItems = () => {
        const items = []
        if (account._id === progress.owner)
            items.push(
                <MenuItem
                    onClick={() => {
                        setState({
                            ...state,
                            editProgressOpen: true,
                        })
                        setAnchorEl(null)
                    }}
                >
                    Edit
                </MenuItem>
            )

        if (account.followProgresses.includes(progress._id))
            items.push(
                <MenuItem
                    onClick={() => {
                        wsActions.unfollowProgress(account._id, progress._id)
                        setAnchorEl(null)
                    }}
                >
                    Unfollow
                </MenuItem>
            )

        if (progress.likes.includes(account._id))
            items.push(
                <MenuItem
                    onClick={() => {
                        wsActions.changeLikesProgress(
                            account._id,
                            progress._id,
                            false
                        )
                        setAnchorEl(null)
                    }}
                >
                    Unlike
                </MenuItem>
            )
        if (
            account.progresses &&
            account.progresses.includes(progress._id.toString())
        )
            items.push(
                <MenuItem
                    onClick={() => {
                        wsActions.leaveProgress(props)
                        setAnchorEl(null)
                    }}
                >
                    Leave
                </MenuItem>
            )
        return items.length > 0 && items
    }

    const dropDownMeuItems = account._id ? getDropDownMeuItems() : null
    return progress ? (
        <>
            <Helmet>
                <title>
                    {progress.name} by {progress.owner} at Addspire
                </title>
                <meta name="description" content={progress.descriptionText} />
            </Helmet>
            <Paper style={{ padding: '15px' }}>
                {progress.images.length > 0 && (
                    <ImageCarousel images={progress.images} />
                )}
                <div style={{ textAlign: 'center', marginTop: '10px' }}>
                    <Typography variant="h4">{progress.name}</Typography>
                    <Box
                        style={{
                            margin: '5px 0px',
                        }}
                    >
                        <Button
                            onClick={() => setFollowersVisible('followers')}
                            style={{ marginRight: '20px' }}
                        >
                            <span
                                style={{
                                    textTransform: 'none',
                                }}
                            >
                                {progress.followingAccounts.length} Followers
                            </span>
                        </Button>
                        <Button onClick={() => setFollowersVisible('likes')}>
                            <span
                                style={{
                                    textTransform: 'none',
                                }}
                            >
                                {progress.likes.length} Likes
                            </span>
                        </Button>
                        <ShareButton
                            title={
                                progress.name +
                                ' goal on Addspire.com - a social network for sharing and supporting ideas, promises and resolutions.'
                            }
                            media={progress.images && progress.images[0]}
                        />
                        {dropDownMeuItems && account && account._id && (
                            <>
                                <IconButton
                                    onClick={event =>
                                        setAnchorEl(event.currentTarget)
                                    }
                                >
                                    <MoreVertOutlined />
                                </IconButton>
                                <Menu
                                    anchorEl={anchorEl}
                                    keepMounted
                                    open={Boolean(anchorEl)}
                                    onClose={() => setAnchorEl(null)}
                                >
                                    {dropDownMeuItems}
                                </Menu>
                            </>
                        )}
                    </Box>
                    <Box
                        style={{
                            margin: '0px 0px 20px',
                        }}
                    >
                        {account &&
                            account._id &&
                            !account.followProgresses.includes(
                                progress._id
                            ) && (
                                <Button
                                    color="primary"
                                    onClick={() =>
                                        wsActions.followProgress(
                                            account._id,
                                            progress._id
                                        )
                                    }
                                    style={{ marginRight: '20px' }}
                                    variant="contained"
                                >
                                    Follow
                                </Button>
                            )}

                        {account &&
                            account._id &&
                            !progress.likes.includes(account._id) && (
                                <Button
                                    color="primary"
                                    variant="contained"
                                    onClick={() =>
                                        wsActions.changeLikesProgress(
                                            account._id,
                                            progress._id,
                                            true
                                        )
                                    }
                                >
                                    Like
                                </Button>
                            )}
                    </Box>
                </div>
            </Paper>
            <Typography
                className={classes.title}
                color="textSecondary"
                gutterBottom
            >
                Tracking
            </Typography>
            <Paper style={{ padding: '15px' }}>
                <ActivityTable
                    progress={progress}
                    account={account}
                    state={state}
                    setState={setState}
                />
            </Paper>
            <Typography
                className={classes.title}
                color="textSecondary"
                gutterBottom
            >
                Description
            </Typography>
            <Paper style={{ padding: '15px' }}>
                {progress.repeat !== 'no' && (
                    <>
                        <p>
                            <RetweetOutlined />
                            {' Repeats '}
                            {getRepeatType(progress.repeat) +
                                (progress.repeat === 'weekday'
                                    ? ' (' +
                                      progress.days.reduce(
                                          (acc, val, index) =>
                                              acc +
                                              (index > 0 ? ', ' : '') +
                                              getDay(val),
                                          ''
                                      ) +
                                      ').'
                                    : '.')}
                        </p>
                        <Divider
                            light
                            variant="middle"
                            style={{ marginBottom: '15px' }}
                        />
                    </>
                )}

                <ReadMore
                    initialHeight={200}
                    contentEditor={progress.description}
                />
            </Paper>
            <Rewards progress={progress} />
            <Typography
                className={classes.title}
                color="textSecondary"
                gutterBottom
            >
                Posts and comments
            </Typography>

            <Dialog
                fullScreen
                open={state.editProgressOpen}
                onClose={() => setState({ ...state, editProgressOpen: false })}
                TransitionComponent={TransitionComponent}
            >
                <EditProgress
                    progress={progress}
                    account={account}
                    state={state}
                    setState={setState}
                    friendData={friendData}
                    cancel={() =>
                        setState({ ...state, editProgressOpen: false })
                    }
                />
            </Dialog>
            <Dialog
                open={Boolean(followersVisible)}
                onClose={() => setFollowersVisible('')}
                title={
                    followersVisible === 'likes'
                        ? 'Goal likes'
                        : 'Goal followers'
                }
                fullWidth
                maxWidth="md"
            >
                <DialogTitle>
                    {followersVisible === 'likes'
                        ? 'Goal likes'
                        : 'Goal followers'}
                </DialogTitle>
                <DialogContent>
                    {progress[
                        followersVisible === 'likes'
                            ? 'likes'
                            : 'followingAccounts'
                    ].map(item => (
                        <MasonryCard
                            friends={true}
                            noLink={true}
                            key={item}
                            actions={
                                <FollowButton account={account} id={item} />
                            }
                            body={
                                <UserButton span={true} id={item} size={50} />
                            }
                        />
                    ))}
                </DialogContent>
                <DialogActions>
                    <Button
                        onClick={() => setFollowersVisible('')}
                        color="primary"
                    >
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
        </>
    ) : null
}

export default withRouter(Activity)
