import { webSocket } from '../containers/ConnectWebsocket/ConnectWebsocket'

export const saveReward = (accountId, reward, wish) => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'saveReward',
                accountId,
                reward,
                wish,
            })
        )
    }
}

