import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import * as wsActions from '../../websocketActions'

import Post from '../../components/Post/Post'
import pullData from '../../utils/pullData'
import Layout from '../../components/Layout/Layout'
import {
    Box,
    Button,
    createStyles,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    IconButton,
    makeStyles,
    Menu,
    MenuItem,
    Paper,
    Typography,
} from '@material-ui/core'
import Welcome from '../../components/Welcome'
import Helmet from 'react-helmet'
import ImageCarousel from '../../components/UI/ImageCarousel/ImageCarousel'
import { MoreVertOutlined } from '@material-ui/icons'
import ReadMore from '../../components/UI/ReadMore'
import FollowButton from '../../components/FollowButton'
import UserButton from '../../components/UserButton/UserButton'
import MasonryCard from '../../components/MasonryCard'
import AddEditReward from '../../components/ProfileRewards/AddEditReward'
import ShareButton from '../../components/UI/ShareButton'
const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            fontSize: 13,
            margin: '30px 15px 10px',
            textTransform: 'uppercase',
            fontWeight: 600,
        },
        pageTitle: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
    })
)
const Reward = props => {
    const classes = useStyles()
    const [likesVisible, setLikesVisible] = useState(false)
    const [anchorEl, setAnchorEl] = useState(null)
    const [deleteVisible, setDeleteVisible] = useState(false)
    const [addRewardOpen, setAddRewardOpen] = useState(false)
    const [data, setData] = useState({})
    const pathParts = props.history.location.pathname.split('/')
    const rewardId =
        props.rewardId || (pathParts.length > 2 ? pathParts[2] : '')
    if (!rewardId) return null
    const wish = pathParts[1] === 'wishlist'
    useEffect(() => {
        if (props.isAuthed) {
            props.setData({ currentReward: rewardId })
        } else {
            pullData(props, data, setData, { forceAuth: false }, data => {
                const friendData = {}
                for (let friend of data.friendData)
                    friendData[friend._id] = friend
                const post = {}
                for (let item of data.posts) post[item._id] = item

                props.setData({
                    friendData,
                    post,
                    account: {},
                    reward: data.reward,
                })
            })
        }
    }, [props.isAuthed, props.history.location.pathname])

    const { reward, account, friendData, posts } = props.isAuthed
        ? props
        : { ...data, posts: props.posts, account: {} }
    if (!reward || !reward._id) return null
    // props.setData({ currentPosts: [reward.post] })

    const getDropDownMeuItems = () => {
        const items = []
        if (account._id === reward.owner)
            items.push(
                <MenuItem
                    onClick={() => {
                        setAddRewardOpen(true)
                        setAnchorEl(null)
                    }}
                >
                    Edit
                </MenuItem>
            )
        items.push(
            <MenuItem
                onClick={() => {
                    setDeleteVisible(true)
                    setAnchorEl(null)
                }}
            >
                Delete
            </MenuItem>
        )

        if (reward.likes.includes(account._id))
            items.push(
                <MenuItem
                    onClick={() => {
                        wsActions.changeLikesReward(
                            account._id,
                            reward._id,
                            false
                        )
                        setAnchorEl(null)
                    }}
                >
                    Unlike
                </MenuItem>
            )

        return items.length > 0 && items
    }

    const dropDownMeuItems = account._id ? getDropDownMeuItems() : null
    return friendData ? (
        <Layout
            header={
                <Typography
                    variant="h6"
                    className={classes.pageTitle}
                    color="inherit"
                    noWrap
                >
                    {reward.name}
                </Typography>
            }
            hide={props.rewardId}
        >
            <Helmet>
                <title>
                    {reward.name || (wish ? 'Wishlist item' : 'Reward')} by{' '}
                    {reward.owner || 'a user'} at Addspire
                </title>
                <meta name="description" content={reward.descriptionText} />
            </Helmet>
            <div className="contentDiv">
                <Paper style={{ padding: '15px' }}>
                    {reward.images.length > 0 && (
                        <ImageCarousel images={reward.images} />
                    )}
                    <div style={{ textAlign: 'center', marginTop: '10px' }}>
                        <Typography variant="h4">{reward.name}</Typography>
                        <Box
                            style={{
                                margin: '5px 0px',
                            }}
                        >
                            <Button onClick={() => setLikesVisible(true)}>
                                <span
                                    style={{
                                        textTransform: 'none',
                                    }}
                                >
                                    {reward.likes.length} Likes
                                </span>
                            </Button>
                            <ShareButton
                                title={
                                    reward.name +
                                    (wish ? ' wishlist item ' : ' reward ') +
                                    'on Addspire.com - a social network for sharing and supporting ideas, promises and resolutions.'
                                }
                                media={reward.images && reward.images[0]}
                            />
                            {dropDownMeuItems && account && account._id && (
                                <>
                                    <IconButton
                                        onClick={event =>
                                            setAnchorEl(event.currentTarget)
                                        }
                                    >
                                        <MoreVertOutlined />
                                    </IconButton>
                                    <Menu
                                        anchorEl={anchorEl}
                                        keepMounted
                                        open={Boolean(anchorEl)}
                                        onClose={() => setAnchorEl(null)}
                                    >
                                        {dropDownMeuItems}
                                    </Menu>
                                </>
                            )}
                        </Box>
                        <Box
                            style={{
                                margin: '0px 0px 20px',
                            }}
                        >
                            {account &&
                                account._id &&
                                !reward.likes.includes(account._id) && (
                                    <Button
                                        color="primary"
                                        variant="contained"
                                        onClick={() =>
                                            wsActions.changeLikesReward(
                                                account._id,
                                                reward._id,
                                                true
                                            )
                                        }
                                    >
                                        Like
                                    </Button>
                                )}
                        </Box>
                    </div>
                    <ReadMore
                        initialHeight={200}
                        contentEditor={reward.description}
                    />
                </Paper>

                <Typography
                    className={classes.title}
                    color="textSecondary"
                    gutterBottom
                >
                    Comments
                </Typography>

                {posts && posts[reward.post] && (
                    <div key={reward.post} style={{ paddingTop: '10px' }}>
                        <Post
                            friendData={friendData}
                            account={account}
                            post={posts[reward.post]}
                            noStartMessage
                            noPostControls
                            showReplies
                        />
                    </div>
                )}
            </div>
            <Dialog
                open={likesVisible}
                onClose={() => setLikesVisible(false)}
                title={wish ? 'Item likes' : 'Reward likes'}
                fullWidth
                maxWidth="md"
            >
                <DialogTitle>Reward likes</DialogTitle>
                <DialogContent>
                    {reward.likes.map(item => (
                        <MasonryCard
                            friends={true}
                            noLink={true}
                            key={item}
                            actions={
                                <FollowButton account={account} id={item} />
                            }
                            body={
                                <UserButton span={true} id={item} size={50} />
                            }
                        />
                    ))}
                </DialogContent>
                <DialogActions>
                    <Button
                        onClick={() => setLikesVisible(false)}
                        color="primary"
                    >
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
            <Dialog open={Boolean(deleteVisible)}>
                <DialogTitle>Delete reward</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Are you sure you want to delete this item?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setDeleteVisible(false)}>
                        Cancel
                    </Button>
                    <Button
                        onClick={() => {
                            wsActions.deleteReward(account._id, reward._id)
                            setDeleteVisible(false)
                            props.history.goBack()
                        }}
                        color="primary"
                    >
                        Delete
                    </Button>
                </DialogActions>
            </Dialog>
            {account._id === reward.owner && (
                <AddEditReward
                    open={addRewardOpen}
                    close={() => setAddRewardOpen(false)}
                    reward={reward}
                />
            )}
        </Layout>
    ) : null
}
const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        posts: state.post,
        postData: state.postData,
        reward: state.reward,
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => {
            dispatch(actions.setData(data))
        },
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Reward)
)
