import React, { useState } from 'react'
import { connect } from 'react-redux'

import InfiniteScroll from 'react-infinite-scroller'
import message from 'antd/es/message'

import MasonryGrid from '../../components/MasonryGrid'
import MasonryCard, {
    getPH,
    placeholderImages,
} from '../../components/MasonryCard'
import Helmet from 'react-helmet'

import 'leaflet/dist/leaflet.css'
import 'esri-leaflet-geocoder/dist/esri-leaflet-geocoder.css'
import 'leaflet-gesture-handling/dist/leaflet-gesture-handling.css'
import {
    Box,
    Button,
    CardActionArea,
    CardContent,
    CardMedia,
    createStyles,
    Divider,
    IconButton,
    InputBase,
    makeStyles,
    Paper,
    Theme,
    Typography,
} from '@material-ui/core'
import { Book, LocationOn, Person, SearchOutlined } from '@material-ui/icons'
import MapSettings from './MapSettings'
import Layout from '../../components/Layout/Layout'
import { Link } from 'react-router-dom'
import ShareButton from '../../components/UI/ShareButton'
import HorizontalScrollList from '../../components/UI/HorizontalScrollList'
import Welcome from '../../components/Welcome'
import axios from 'axios'
import { useTranslation } from 'react-i18next'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            padding: '2px 4px',
            display: 'flex',
            alignItems: 'center',
            maxWidth: 400,
            width: '100%',
        },
        input: {
            flex: 1,
        },
        iconButton: {
            padding: 10,
        },
        divider: {
            height: 28,
            margin: 4,
        },
        appBar: {
            position: 'relative',
        },
        title: {
            fontSize: 13,
            margin: '30px 15px 10px',
            textTransform: 'uppercase',
            fontWeight: 600,
        },
    })
)

export const sortProgresses = (a, b) => {
    const valA = a.views || 0
    const valB = b.views || 0
    return valA > valB ? -1 : 1
}
const Explore = props => {
    const { t } = useTranslation()

    const { account, progressData } = props
    const [isSearching, setIsSearching] = useState(false)
    const [mapVisible, setMapVisible] = useState(false)
    const [foundProgresses, setFoundProgresses] = useState([])
    const [progresses, setProgresses] = useState([])
    const [noMore, setNoMore] = useState(false)
    const [noMorePopular, setNoMorePopular] = useState(false)

    const [search, setSearch] = useState({
        value: '',
        withMap: false,
        distance: 20,
        zoom: 7,
        position: [40.689078, -74.044526],
        units: 'mi',
    })

    const [value, setValue] = useState('')

    const onSearch = (value, noNoMore) => {
        if (!noMore || noNoMore) {
            setNoMore(true)
            axios
                .post('/api/explore/search', {
                    value: value ? { ...search, value } : search,
                    skip: noNoMore ? 0 : foundProgresses.length,
                })
                .then(res => {
                    if (res.data.success) {
                        setNoMore(res.data.noMore)
                        setFoundProgresses(res.data.progresses)
                        if (res.data.progresses.length === 0)
                            message.warning('There are no progresses found.')
                        const newFriendData = {}
                        for (let friend of res.data.friends) {
                            newFriendData[friend._id] = friend
                        }
                    } else {
                        if (res.data.errorCode) {
                            message.error(
                                'Something is wrong with the data. Please try one more time with new data.'
                            )
                        }
                    }
                })
                .catch(err => {})
        }
    }
    const onGetPopular = () => {
        if (!noMorePopular) {
            setNoMorePopular(true)
            axios
                .post('/api/explore/popular', {
                    skip: progresses.length,
                })
                .then(res => {
                    if (res.data.success) {
                        setNoMorePopular(res.data.noMore)
                        setProgresses([...progresses, ...res.data.progresses])
                        const newFriendData = {}
                        for (let friend of res.data.friends) {
                            newFriendData[friend._id] = friend
                        }
                    } else {
                        if (res.data.errorCode) {
                            message.error(
                                'Something is wrong with the data. Please try one more time with new data.'
                            )
                        }
                    }
                })
                .catch(err => {})
        }
    }

    const classes = useStyles()

    let loadedProgresses = []
    if (account._id && account.recentProgresses) {
        loadedProgresses = account.recentProgresses
            .filter(item => progressData[item])
            .map(item => progressData[item])
    }
    console.log(window.navigator)
    return (
        <Layout white>
            <div>
                <Helmet>
                    <title>
                        Explore goals and ideas from around the World!
                    </title>
                    <meta
                        name="description"
                        content="Addspire is a social network where you can inspire and motivate yourself or the people you care."
                    />
                </Helmet>

                <MapSettings
                    mapVisible={mapVisible}
                    setMapVisible={setMapVisible}
                    setIsSearching={setIsSearching}
                    setNoMore={setNoMore}
                    setFoundProgresses={setFoundProgresses}
                    onSearch={onSearch}
                    onGetPopular={onGetPopular}
                    setSearch={setSearch}
                    search={search}
                />
                <Welcome />

                <Paper className={classes.root} variant="outlined">
                    <InputBase
                        className={classes.input}
                        placeholder="Search goals"
                        onChange={e => {
                            if (!e.target.value && !search.withMap) {
                                setNoMore(false)
                                setFoundProgresses([])
                                setIsSearching(false)
                                setSearch({ ...search, value: '' })
                            }
                            setValue(e.target.value)
                        }}
                    />
                    <IconButton
                        className={classes.iconButton}
                        onClick={() => {
                            if (value) {
                                setIsSearching(true)
                                if (value !== search.value) {
                                    setNoMore(false)
                                    setFoundProgresses([])
                                    setSearch({ ...search, value })
                                    onSearch(value, true)
                                }
                            } else {
                                setNoMore(false)
                                setSearch({ ...search, value })
                                setIsSearching(false)
                            }
                        }}
                    >
                        <SearchOutlined />
                    </IconButton>
                    <Divider
                        className={classes.divider}
                        orientation="vertical"
                    />
                    <IconButton
                        className={classes.iconButton}
                        onClick={() => setMapVisible(true)}
                        color={search.withMap ? 'primary' : 'default'}
                    >
                        <LocationOn />
                    </IconButton>
                </Paper>
                {/* <Typography>{t('Welcome to React')}</Typography> */}
                <HorizontalScrollList
                    title="Recently viewed goals by you"
                    items={loadedProgresses}
                    content={item => (
                        <CardActionArea>
                            <Link to={'/goals/' + item._id}>
                                <CardMedia
                                    style={{
                                        height: '150px',
                                        backgroundSize: 'contain',
                                    }}
                                    image={
                                        item.images.length > 0
                                            ? item.images[0]
                                            : placeholderImages[getPH(item._id)]
                                    }
                                    title={item.name}
                                />
                                <CardContent style={{ padding: '5px' }}>
                                    <Typography variant="body2" noWrap>
                                        {item.name}
                                    </Typography>
                                </CardContent>
                            </Link>
                        </CardActionArea>
                    )}
                />
                {/* {loadedProgresses.length > 0 ? ( */}
                <Typography
                    className={classes.title}
                    color="textSecondary"
                    gutterBottom
                >
                    Top goals and resolutions
                </Typography>
                {/* ) : null} */}
                {isSearching ? (
                    foundProgresses.length > 0 ? (
                        <InfiniteScroll
                            pageStart={0}
                            loadMore={onSearch()}
                            hasMore={!noMore}
                            loader={
                                <div className="loader" key={0}>
                                    Loading ...
                                </div>
                            }
                        >
                            <MasonryGrid>
                                {foundProgresses
                                    .sort(sortProgresses)
                                    .map((item, index) => (
                                        <MasonryCard
                                            id={item._id}
                                            key={index}
                                            onClickLink={'/goals/' + item._id}
                                            images={item.images}
                                            body={
                                                <>
                                                    {/* <UserButton
                                                noName={true}
                                                id={item.owner}
                                                friendDataExp={friendData}
                                            /> */}
                                                    <span
                                                        style={{
                                                            fontWeight: 'bold',
                                                        }}
                                                    >
                                                        {item.name}
                                                    </span>
                                                </>
                                            }
                                            actions={
                                                <ShareButton
                                                    link={
                                                        'https://addspire.com/goals/' +
                                                        item._id
                                                    }
                                                    title={
                                                        item.name +
                                                        ' goal on Addspire.com - a social network for sharing and supporting ideas, promises and resolutions.'
                                                    }
                                                    media={
                                                        item.images &&
                                                        item.images[0]
                                                    }
                                                />
                                            }
                                        />
                                    ))}
                            </MasonryGrid>
                        </InfiniteScroll>
                    ) : null
                ) : (
                    <InfiniteScroll
                        pageStart={0}
                        loadMore={onGetPopular}
                        hasMore={!noMorePopular}
                        loader={
                            <div className="loader" key={0}>
                                Loading ...
                            </div>
                        }
                    >
                        <MasonryGrid>
                            {progresses
                                .sort(sortProgresses)
                                .map((item, index) => (
                                    <MasonryCard
                                        id={item._id}
                                        key={index}
                                        onClickLink={'/goals/' + item._id}
                                        images={item.images}
                                        body={
                                            <>
                                                {/* <UserButton
                                            noName={true}
                                            id={item.owner}
                                            friendDataExp={friendData}
                                        /> */}
                                                <span
                                                    style={{
                                                        fontWeight: 'bold',
                                                    }}
                                                >
                                                    {item.name}
                                                </span>
                                            </>
                                        }
                                        actions={
                                            <ShareButton
                                                link={
                                                    'https://addspire.com/goals/' +
                                                    item._id
                                                }
                                                title={
                                                    item.name +
                                                    ' goal on Addspire.com - a social network for sharing and supporting ideas, promises and resolutions.'
                                                }
                                                media={
                                                    item.images &&
                                                    item.images[0]
                                                }
                                            />
                                        }
                                    />
                                ))}
                        </MasonryGrid>
                    </InfiniteScroll>
                )}
            </div>
        </Layout>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        progressData: state.progressData,
    }
}

export default connect(mapStateToProps)(Explore)
