import React, { useEffect, useState, useRef } from 'react'

import { Map, TileLayer } from 'react-leaflet'
import 'leaflet/dist/leaflet.css'

import * as ELG from 'esri-leaflet-geocoder'
import 'esri-leaflet-geocoder/dist/esri-leaflet-geocoder.css'
import LocateControl from './LocateControl'
import { GestureHandling } from 'leaflet-gesture-handling'
import 'leaflet-gesture-handling/dist/leaflet-gesture-handling.css'
import {
    AppBar,
    createStyles,
    Dialog,
    IconButton,
    makeStyles,
    Slide,
    Theme,
    Toolbar,
    Typography,
    Button,
    Grid,
    Slider,
    MenuItem,
    Select,
    Switch,
} from '@material-ui/core'
import { Close } from '@material-ui/icons'
const TransitionComponent = props => <Slide direction="up" {...props} />
const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            padding: '2px 4px',
            display: 'flex',
            alignItems: 'center',
            maxWidth: 400,
            width: '100%',
        },
        input: {
            marginLeft: theme.spacing(1),
            flex: 1,
        },
        iconButton: {
            padding: 10,
        },
        divider: {
            height: 28,
            margin: 4,
        },
        appBar: {
            position: 'fixed',
        },
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
    })
)
const MapSettings = props => {
    const {
        mapVisible,
        setMapVisible,
        setIsSearching,
        setNoMore,
        setFoundProgresses,
        onSearch,
        onGetPopular,
        setSearch,
        search,
    } = props
    const map = useRef(null)
    const [radius, setRadius] = useState(false)

    useEffect(() => {
        if (map && map.current && map.current.leafletElement) {
            new ELG.Geosearch().addTo(map.current.leafletElement)
            new GestureHandling(map.current.leafletElement)
        }
    }, [])

    const mapDragEnd = e => {
        setSearch({
            ...search,
            zoom: e.target._zoom,
            position: [e.target.getCenter().lat, e.target.getCenter().lng],
            withMap: true,
        })
    }

    useEffect(() => {
        getRadius()
    }, [props.search])

    const getRadius = () => {
        if (map && map.current && map.current.leafletElement) {
            const metresPerPixel =
                (40075016.686 *
                    Math.abs(
                        Math.cos(
                            (map.current.leafletElement.getCenter().lat *
                                Math.PI) /
                                180
                        )
                    )) /
                Math.pow(2, map.current.leafletElement.getZoom() + 8)

            setRadius(
                search.units === 'mi'
                    ? (search.distance * 1609) / metresPerPixel
                    : (search.distance * 1000) / metresPerPixel
            )
        }
    }

    const classes = useStyles()

    return (
        <Dialog
            fullScreen
            open={mapVisible}
            onClose={() => {
                setMapVisible(false)
                if (search.withMap || search.value) {
                    setIsSearching(true)
                    setNoMore(false)
                    setFoundProgresses([])
                    onSearch()
                } else {
                    onGetPopular()
                }
            }}
            TransitionComponent={TransitionComponent}
        >
            <AppBar className={classes.appBar} color="primary">
                <Toolbar>
                    <IconButton
                        edge="start"
                        color="inherit"
                        onClick={() => {
                            setMapVisible(false)
                            if (search.withMap || search.value) {
                                setIsSearching(true)
                                setNoMore(false)
                                setFoundProgresses([])
                                onSearch()
                            } else {
                                onGetPopular()
                            }
                        }}
                        aria-label="close"
                    >
                        <Close />
                    </IconButton>
                    <Typography
                        variant="h6"
                        className={classes.title}
                        color="inherit"
                    >
                        Search with location
                    </Typography>
                    <Button
                        autoFocus
                        color="inherit"
                        onClick={() => {
                            setMapVisible(false)
                            if (search.withMap || search.value) {
                                setIsSearching(true)
                                setNoMore(false)
                                setFoundProgresses([])
                                onSearch()
                            } else {
                                onGetPopular()
                            }
                        }}
                    >
                        Done
                    </Button>
                </Toolbar>
            </AppBar>
            <Toolbar />

            <Grid
                container
                style={{ padding: '15px', maxWidth: '800px' }}
                alignItems="center"
            >
                <Grid item xs={4} sm={4}>
                    Search with location
                </Grid>
                <Grid item xs={8} sm={8}>
                    <Switch
                        checked={search.withMap}
                        color="primary"
                        onChange={e => {
                            setSearch({
                                ...search,
                                withMap: e.target.checked,
                            })
                        }}
                    />
                </Grid>
                <Grid item xs={4}>
                    Distance
                </Grid>
                <Grid item xs={8}>
                    <Slider
                        valueLabelDisplay="auto"
                        min={1}
                        max={500}
                        onChange={(e, distance) =>
                            setSearch({ ...search, distance })
                        }
                        value={search.distance}
                    />
                </Grid>
                <Grid item xs={4}>
                    Units
                </Grid>
                <Grid item xs={8}>
                    <Select
                        value={search.units}
                        onChange={event =>
                            setSearch({ ...search, units: event.target.value })
                        }
                    >
                        <MenuItem value="mi">mi</MenuItem>
                        <MenuItem value="km">km</MenuItem>
                    </Select>
                </Grid>
            </Grid>

            <div
                style={{
                    position: 'relative',
                    minHeight: '300px',
                    flex: 1,
                    marginTop: '30px',
                    overflow: 'hidden',
                }}
            >
                <Map
                    whenReady={() => {
                        getRadius()
                    }}
                    ref={map}
                    center={search.position}
                    zoom={search.zoom}
                    style={{
                        width: '100%',
                        height: '100%',
                        position: 'relative',
                        zIndex: 1,
                    }}
                    ondragend={mapDragEnd}
                    onmoveend={mapDragEnd}
                    gestureHandling={true}
                >
                    <TileLayer
                        attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    />
                    <LocateControl />
                </Map>
                <div
                    style={{
                        backgroundImage: `url(${require('../../assets/images/pin.png')})`,
                        width: '20px',
                        height: '35px',
                        position: 'absolute',
                        zIndex: 3,
                        left: 'calc(50% - 10px)',
                        top: 'calc(50% - 35px)',
                        pointerEvents: 'none',
                        touchAction: 'none',
                    }}
                />
                <div
                    style={{
                        background: 'rgba(82, 95, 165, .15)',
                        width: 2 * radius + 'px',
                        height: 2 * radius + 'px',
                        position: 'absolute',
                        zIndex: '2',
                        left: 'calc(50% - ' + radius + 'px)',
                        top: 'calc(50% - ' + radius + 'px)',
                        pointerEvents: 'none',
                        touchAction: 'none',
                        borderRadius: radius + 'px',
                        border: '1px rgba(82, 95, 165, .5) solid',
                    }}
                />
            </div>
        </Dialog>
    )
}

export default MapSettings
