import React, { useState } from 'react'

import * as wsActions from '../../websocketActions'
import UserButton from '../../components/UserButton/UserButton'
import Tabs from 'antd/es/tabs'
import Badge from 'antd/es/badge'
import moment from 'moment'
import {
    Button,
    CardContent,
    CardMedia,
    Chip,
    createMuiTheme,
    List,
    ListItem,
    ListItemAvatar,
    ListItemSecondaryAction,
    ListItemText,
    MenuItem,
    MuiThemeProvider,
    Select,
    Typography,
} from '@material-ui/core'
import { blue, green, orange, purple, red } from '@material-ui/core/colors'
import { fade } from '@material-ui/core/styles/colorManipulator'
import HorizontalScrollList from '../../components/UI/HorizontalScrollList'
import { getPH, placeholderImages } from '../../components/MasonryCard'
import { Link, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

moment().format()

const redTheme = createMuiTheme({
    palette: { primary: { main: red[600] } },
    overrides: {
        MuiSelect: {
            selectMenu: { color: red[600] },
            select: {
                '&:focus': {
                    backgroundColor: fade(red[600], 0.05),
                },
            },
        },
    },
})
const blueTheme = createMuiTheme({
    palette: { primary: { main: blue[600] } },
    overrides: {
        MuiSelect: {
            selectMenu: { color: blue[600] },
            select: {
                '&:focus': {
                    backgroundColor: fade(blue[600], 0.05),
                },
            },
        },
    },
})
const greenTheme = createMuiTheme({
    palette: { primary: { main: green[600] } },
    overrides: {
        MuiSelect: {
            selectMenu: { color: green[600] },
            select: {
                '&:focus': {
                    backgroundColor: fade(green[600], 0.05),
                },
            },
        },
    },
})
const orangeTheme = createMuiTheme({
    palette: { primary: { main: orange[600] } },
    overrides: {
        MuiSelect: {
            selectMenu: { color: orange[600] },
            select: {
                '&:focus': {
                    backgroundColor: fade(orange[600], 0.05),
                },
            },
        },
    },
})

const customTheme = createMuiTheme({
    palette: {
        primary: {
            main: purple[500],
        },
    },
    overrides: {
        MuiListItem: {
            secondaryAction: {
                paddingRight: '133px',
                paddingLeft: 0,
            },
        },
    },
})

const ActivityTable = props => {
    const [showEarnedRewards, setShowEarnedRewards] = useState(false)
    const { progress, account, rewardData } = props

    const isOwner = account && account._id === progress.owner

    let lastStages = progress.stages
        .slice(Math.max(progress.stages.length - 7, 0))
        .sort((a, b) => {
            let aS, aF, bS, bF
            if (a.old && !b.old) return 1
            else if (b.old && !a.old) return -1
            else {
                if (a.day) {
                    aS = moment()
                        .year(a.year)
                        .dayOfYear(a.day)
                        .startOf('day')
                    aF = moment()
                        .year(a.year)
                        .dayOfYear(a.day)
                        .endOf('day')
                    bS = moment()
                        .year(b.year)
                        .dayOfYear(b.day)
                        .startOf('day')
                    bF = moment()
                        .year(b.year)
                        .dayOfYear(b.day)
                        .endOf('day')
                } else if (a.week) {
                    aS = moment()
                        .year(a.year)
                        .week(a.week)
                        .startOf('week')
                    aF = moment()
                        .year(a.year)
                        .week(a.week)
                        .endOf('week')
                    bS = moment()
                        .year(b.year)
                        .week(b.week)
                        .startOf('week')
                    bF = moment()
                        .year(b.year)
                        .week(b.week)
                        .endOf('week')
                } else if (a.month) {
                    aS = moment()
                        .year(a.year)
                        .month(a.month)
                        .startOf('month')
                    aF = moment()
                        .year(a.year)
                        .month(a.month)
                        .endOf('month')
                    bS = moment()
                        .year(b.year)
                        .month(b.month)
                        .startOf('month')
                    bF = moment()
                        .year(b.year)
                        .month(b.month)
                        .endOf('month')
                } else if (a.year) {
                    aS = moment()
                        .year(a.year)
                        .startOf('year')
                    aF = moment()
                        .year(a.year)
                        .endOf('year')
                    bS = moment()
                        .year(b.year)
                        .startOf('year')
                    bF = moment()
                        .year(b.year)
                        .endOf('year')
                }
                if (aS) {
                    const now = moment()

                    if (
                        Math.min(
                            Math.abs(now.diff(aS)),
                            Math.abs(now.diff(aF))
                        ) <=
                        Math.min(Math.abs(now.diff(bS)), Math.abs(now.diff(bF)))
                    )
                        return -1
                    else return 1
                }
            }
            return 0
        })

    const nowStageId = lastStages.length > 0 ? lastStages[0].stageId : 0

    lastStages = progress.stages

    return lastStages.length === 0 ? null : (
        <MuiThemeProvider theme={customTheme}>
            <Tabs
                // activeKey={activeTab}
                type="card"
                defaultActiveKey={
                    nowStageId
                        ? lastStages
                              .findIndex(item => item.stageId === nowStageId)
                              .toString()
                        : '2'
                }
                // onTabClick={key => setActiveTab(key === activeTab ? -1 : key)}
            >
                {lastStages.map((stage, index) => {
                    const getStatus = id => {
                        if (isOwner && stage.dismissed) return 'dismissed'
                        if (stage.failBy.find(item => item.accountId === id))
                            return 'fail'
                        if (
                            stage.approvedBy.find(item => item.accountId === id)
                        )
                            return 'complete'
                        return 'process'
                    }

                    const getStatusText = id => {
                        const status = getStatus(id)
                        switch (status) {
                            case 'fail':
                                return 'Failed'
                            case 'complete':
                                return 'Complete'
                            case 'dismissed':
                                return 'Dismissed'
                            default:
                                return 'In process'
                        }
                    }
                    const myStatus =
                        account && account._id && getStatus(account._id)

                    const stageStatus = getStatus(progress.owner)

                    const getTag = id => {
                        return id === account._id ? (
                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                value={myStatus}
                                onChange={e =>
                                    wsActions.changeStage(
                                        e.target.value,
                                        account._id,
                                        progress._id,
                                        stage.stageId
                                    )
                                }
                            >
                                <MenuItem value="complete">Complete</MenuItem>
                                <MenuItem value="process">In process</MenuItem>
                                <MenuItem value="fail">Fail</MenuItem>
                                <MenuItem value="dismissed">Dismissed</MenuItem>
                            </Select>
                        ) : (
                            <Chip color="primary" label={getStatusText(id)} />
                        )
                    }

                    const users = progress.users
                        .filter(item => item !== progress.owner)
                        .map(user => {
                            const userStatus = getStatus(user)
                            return (
                                <ListItem>
                                    <ListItemAvatar>
                                        <UserButton noName={true} id={user} />
                                    </ListItemAvatar>
                                    <ListItemText
                                        primary={
                                            <UserButton
                                                id={user}
                                                noAva={true}
                                                span={true}
                                                onlyName={true}
                                            />
                                        }
                                    />
                                    <ListItemSecondaryAction>
                                        <MuiThemeProvider
                                            theme={
                                                userStatus === 'process'
                                                    ? blueTheme
                                                    : userStatus === 'complete'
                                                    ? greenTheme
                                                    : userStatus === 'fail'
                                                    ? redTheme
                                                    : orangeTheme
                                            }
                                        >
                                            {getTag(user)}
                                        </MuiThemeProvider>
                                    </ListItemSecondaryAction>
                                </ListItem>
                            )
                        })

                    const owner = [
                        <ListItem>
                            <ListItemAvatar>
                                <UserButton noName={true} id={progress.owner} />
                            </ListItemAvatar>
                            <ListItemText
                                primary={
                                    <Typography noWrap variant="body2">
                                        <UserButton
                                            id={progress.owner}
                                            noAva={true}
                                            span={true}
                                            onlyName={true}
                                        />
                                    </Typography>
                                }
                            />
                            <ListItemSecondaryAction>
                                <MuiThemeProvider
                                    theme={
                                        stageStatus === 'process'
                                            ? blueTheme
                                            : stageStatus === 'complete'
                                            ? greenTheme
                                            : stageStatus === 'fail'
                                            ? redTheme
                                            : orangeTheme
                                    }
                                >
                                    {getTag(progress.owner)}
                                </MuiThemeProvider>
                            </ListItemSecondaryAction>
                        </ListItem>,
                    ]
                    let stageDate
                    let tabTitle = <div>One time goal</div>
                    if (stage.day) {
                        stageDate = moment()
                            .year(stage.year)
                            .dayOfYear(stage.day)
                        tabTitle = (
                            <>
                                <div>{stageDate.format('dddd')}</div>
                                <div>{stageDate.format('D MMM YY')}</div>
                            </>
                        )
                    } else if (stage.week) {
                        stageDate = moment()
                            .year(stage.year)
                            .week(stage.week)
                        tabTitle = (
                            <>
                                <div>{stageDate.format('wo')} week</div>
                                <div>
                                    {stageDate
                                        .startOf('week')
                                        .format('D MMM YY')}
                                </div>
                                <div>
                                    {stageDate.endOf('week').format('D MMM YY')}
                                </div>
                            </>
                        )
                    } else if (stage.month) {
                        stageDate = moment()
                            .year(stage.year)
                            .month(stage.month)
                        tabTitle = (
                            <>
                                <div>{stageDate.format('MMMM')}</div>
                                <div>{stageDate.format('YYYY')}</div>
                            </>
                        )
                    } else if (stage.year) {
                        stageDate = moment().year(stage.year)

                        tabTitle = <div>{stageDate.format('YYYY')}</div>
                    }

                    return (
                        <Tabs.TabPane
                            tab={
                                <Badge
                                    color={
                                        stageStatus === 'complete'
                                            ? 'green'
                                            : stageStatus === 'dismissed'
                                            ? 'orange'
                                            : stageStatus === 'fail'
                                            ? 'volcano'
                                            : 'blue'
                                    }
                                >
                                    <span
                                        style={{
                                            fontSize: '12px',
                                            textTransform: 'uppercase',
                                            textAlign: 'center',
                                        }}
                                    >
                                        {tabTitle}
                                    </span>
                                </Badge>
                            }
                            key={index}
                        >
                            <List dense={true}>
                                {owner}
                                {users}
                            </List>

                            {stage.paid.length > 0 && (
                                <>
                                    {/* {showEarnedRewards && (
                                        // <Rewards
                                        //     rewards={stage.paid}
                                        //     readOnly
                                        // />
                                    )} */}
                                    <Button
                                        onClick={() =>
                                            setShowEarnedRewards(
                                                !showEarnedRewards
                                            )
                                        }
                                        color="primary"
                                        style={{}}
                                    >
                                        {(showEarnedRewards ? 'Hide' : 'Show') +
                                            ' earned rewards'}
                                    </Button>
                                    {showEarnedRewards && (
                                        <HorizontalScrollList
                                            items={stage.paid
                                                .map(item => rewardData[item])
                                                .filter(item => item)}
                                            content={item => (
                                                <CardContent
                                                    style={{
                                                        textAlign: 'center',
                                                        position: 'relative',
                                                        padding: '5px',
                                                    }}
                                                >
                                                    <Link
                                                        to={
                                                            '/rewards/' +
                                                            item._id
                                                        }
                                                    >
                                                        <CardMedia
                                                            style={{
                                                                height: '150px',
                                                                backgroundSize:
                                                                    'contain',
                                                            }}
                                                            image={
                                                                item.images
                                                                    .length > 0
                                                                    ? item
                                                                          .images[0]
                                                                    : placeholderImages[
                                                                          getPH(
                                                                              item._id
                                                                          )
                                                                      ]
                                                            }
                                                            title={item.name}
                                                        />
                                                        <CardContent
                                                            style={{
                                                                padding: '5px',
                                                            }}
                                                        >
                                                            <Typography
                                                                variant="body2"
                                                                noWrap
                                                            >
                                                                {item.name}
                                                            </Typography>
                                                        </CardContent>
                                                    </Link>
                                                </CardContent>
                                            )}
                                        />
                                    )}
                                </>
                            )}
                        </Tabs.TabPane>
                    )
                })}
            </Tabs>
        </MuiThemeProvider>
    )
}

const mapStateToProps = state => {
    return {
        rewardData: state.rewardData,
    }
}

export default withRouter(connect(mapStateToProps)(ActivityTable))
