import {
    Box,
    Card,
    CardActionArea,
    CardContent,
    createStyles,
    makeStyles,
    Paper,
    Theme,
    Typography,
} from '@material-ui/core'
import { AddCircle } from '@material-ui/icons'
import Empty from 'antd/es/empty'
import React from 'react'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            fontSize: 13,
            margin: '30px 15px 10px',
            textTransform: 'uppercase',
            fontWeight: 600,
        },
        addBox: {
            color: theme.palette.primary.main,
            display: 'flex',
            alignItems: 'center',
        },
    })
)
export default props => {
    const classes = useStyles()
    return props.items.length > 0 ||
        props.empty ||
        props.customEmpty ||
        props.withAdd ? (
        <>
            {props.title && (
                <Typography
                    className={classes.title}
                    color="textSecondary"
                    gutterBottom
                >
                    {props.title}
                </Typography>
            )}
            <Paper
                style={{
                    padding: '15px',
                    display: 'flex',
                    overflowX: 'auto',
                    paddingBottom: '10px',
                }}
            >
                {props.items.length === 0 && props.empty ? (
                    props.customEmpty || <Empty />
                ) : (
                    <>
                        {props.withAdd && (
                            <Card
                                style={{
                                    borderRadius: '15px',
                                    cursor: 'pointer',
                                    flexShrink: 0,
                                    width: '100px',
                                    marginRight: '10px',
                                    position: 'relative',
                                    minHeight: '160px',
                                }}
                            >
                                <CardActionArea
                                    style={{
                                        height: '100%',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                    }}
                                    onClick={() => props.onAdd()}
                                >
                                    <CardContent
                                        style={{
                                            flex: 1,
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'center',
                                            padding: '5px',
                                        }}
                                    >
                                        <Box
                                            style={{
                                                flexDirection: 'column',
                                            }}
                                            className={classes.addBox}
                                        >
                                            <AddCircle />
                                            <Box
                                                style={{ textAlign: 'center' }}
                                            >
                                                {props.addTitle || 'Add'}
                                            </Box>
                                        </Box>
                                    </CardContent>
                                </CardActionArea>
                            </Card>
                        )}
                        {props.items.map((item, index) => (
                            <Card
                                style={{
                                    borderRadius: '15px',
                                    cursor: 'pointer',
                                    flexShrink: 0,
                                    width: '100px',
                                    marginRight: '10px',
                                    position: 'relative',
                                }}
                            >
                                {props.content(item, index)}
                            </Card>
                        ))}
                        <Box style={{ width: '10px', flexShrink: 0 }} />
                    </>
                )}
            </Paper>
        </>
    ) : null
}
