import React, { useEffect, useState } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as wsActions from '../../websocketActions'
import HorizontalScrollList from '../UI/HorizontalScrollList'
import {
    Box,
    CardContent,
    CardMedia,
    Dialog,
    DialogActions,
    DialogContent,
    IconButton,
    Typography,
} from '@material-ui/core'
import { getPH, placeholderImages } from '../MasonryCard'
import AddEditReward from './AddEditReward'
import Reward from '../../containers/Reward/Reward'
import { Close } from '@material-ui/icons'

const ProfileRewards = props => {
    const [addRewardOpen, setAddRewardOpen] = useState(false)
    const [rewardPreview, setRewardPreview] = useState('')

    const { account, owner, rewardData, wish, additionalButton } = props
    const isOwner = account && account._id && account._id === owner._id
    return (
        <>
            <HorizontalScrollList
                title={wish ? 'Wishlist' : 'Rewards'}
                items={owner[wish ? 'rewards' : 'rewards']
                    .map(item => rewardData[item])
                    .filter(
                        item => item && Boolean(wish) === Boolean(item.wish)
                    )}
                content={item => {
                    const content = (
                        <>
                            {' '}
                            <CardMedia
                                style={{
                                    height: '150px',
                                    backgroundSize: 'contain',
                                }}
                                image={
                                    item.images.length > 0
                                        ? item.images[0]
                                        : placeholderImages[getPH(item._id)]
                                }
                                title={item.name}
                            />
                            <CardContent style={{ padding: '5px' }}>
                                <Typography variant="body2" noWrap>
                                    {item.name}
                                </Typography>
                            </CardContent>
                        </>
                    )
                    return (
                        <CardContent
                            style={{
                                textAlign: 'center',
                                position: 'relative',
                                padding: '5px',
                            }}
                        >
                            {props.openInDialog ? (
                                <Box onClick={() => setRewardPreview(item._id)}>
                                    {content}
                                </Box>
                            ) : (
                                <Link
                                    to={
                                        (wish ? '/wishlist/' : '/rewards/') +
                                        item._id
                                    }
                                >
                                    {content}
                                </Link>
                            )}
                            {additionalButton && additionalButton(item)}
                        </CardContent>
                    )
                }}
                withAdd={isOwner}
                addTitle={wish ? 'Add item' : 'Add reward'}
                onAdd={() => setAddRewardOpen(true)}
            />
            {isOwner && (
                <AddEditReward
                    open={addRewardOpen}
                    close={() => setAddRewardOpen(false)}
                    wish={wish}
                />
            )}
            {props.openInDialog && (
                <Dialog
                    open={Boolean(rewardPreview)}
                    onClose={() => setRewardPreview('')}
                    fullWidth
                    maxWidth="md"
                >
                    <DialogActions>
                        <IconButton onClick={() => setRewardPreview('')}>
                            <Close />
                        </IconButton>
                    </DialogActions>
                    <DialogContent>
                        {rewardPreview && <Reward rewardId={rewardPreview} />}
                    </DialogContent>
                </Dialog>
            )}
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        friendData: state.friendData,
        rewardData: state.rewardData,
    }
}

export default withRouter(connect(mapStateToProps)(ProfileRewards))
