import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import * as wsActions from '../../websocketActions'
import {
    AppBar,
    Button,
    Dialog,
    IconButton,
    Input,
    makeStyles,
    Slide,
    Toolbar,
    Typography,
} from '@material-ui/core'
import { Close } from '@material-ui/icons'
import Form from 'antd/es/form/Form'
import ImagePickerMulti from '../UI/ImagePickerMulti'
import TinyEditor from '../UI/TinyEditor'
import { nameRules } from '../../constants/fieldRules'

const TransitionComponent = props => <Slide direction="left" {...props} />
const useStyles = makeStyles(theme => ({
    root: {
        // maxWidth: 345,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },

    appBar: {
        position: 'fixed',
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1,
    },
}))

const layout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 20 },
}

const AddEditReward = props => {
    const classes = useStyles()
    const [editedReward, setEditedReward] = useState(
        props.reward || { images: [] }
    )
    useEffect(() => {
        setEditedReward(props.reward || { images: [] })
    }, props.open)
    const { wish } = props
    return (
        <Dialog
            fullScreen
            open={props.open}
            onClose={props.close}
            TransitionComponent={TransitionComponent}
        >
            {props.open && (
                <>
                    <AppBar className={classes.appBar} color="primary">
                        <Toolbar>
                            <IconButton
                                edge="start"
                                color="inherit"
                                onClick={props.close}
                                aria-label="close"
                            >
                                <Close />
                            </IconButton>
                            <Typography
                                variant="h6"
                                className={classes.title}
                                color="inherit"
                            >
                                {(!props.reward ? 'Add new ' : 'Edit ') +
                                    (wish ? 'item' : 'reward')}
                            </Typography>
                            <Button
                                autoFocus
                                color="inherit"
                                onClick={() => {
                                    wsActions.saveReward(
                                        props.account._id,
                                        editedReward,
                                        wish
                                    )
                                    props.close()
                                }}
                            >
                                Save
                            </Button>
                        </Toolbar>
                    </AppBar>
                    <Toolbar />
                    <Form
                        style={{
                            maxWidth: '1200px',
                            margin: '0 auto',
                            padding: '15px',
                            width: '100%',
                        }}
                        name="new_reward"
                        {...layout}
                    >
                        <Form.Item
                            name="images"
                            label="Images"
                            // valuePropName="images"
                        >
                            <ImagePickerMulti
                                images={editedReward.images}
                                onValueChange={images =>
                                    setEditedReward({
                                        ...editedReward,
                                        images,
                                    })
                                }
                            />
                        </Form.Item>

                        <Form.Item label="Name" rules={nameRules}>
                            <Input
                                fullWidth={true}
                                value={editedReward.name}
                                onChange={({ target: { value } }) =>
                                    setEditedReward({
                                        ...editedReward,
                                        name: value,
                                    })
                                }
                            />
                        </Form.Item>
                        <Form.Item label="Description">
                            <TinyEditor
                                value={editedReward.description}
                                onEditorChange={value => {
                                    setEditedReward(values => ({
                                        ...values,
                                        description: value,
                                    }))
                                }}
                                onEditorChangePlain={value =>
                                    setEditedReward(values => ({
                                        ...values,
                                        descriptionText: value,
                                    }))
                                }
                            />
                        </Form.Item>
                    </Form>
                </>
            )}
        </Dialog>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
    }
}

export default connect(mapStateToProps)(AddEditReward)
