import React, { useEffect, useState } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Avatar from 'antd/es/avatar'

import FriendActionButton from '../../components/FriendActionButton'

import getAvatar from '../../utils/getAvatar'
import pullData from '../../utils/pullData'
import UserButton from '../../components/UserButton/UserButton'
import MasonryGrid from '../../components/MasonryGrid'

import MasonryCard, {
    getPH,
    placeholderImages,
} from '../../components/MasonryCard'
import ProgressButton from '../../components/ProgressButton'
import FollowButton from '../../components/FollowButton'

import * as wsActions from '../../websocketActions'
import Upload from 'antd/es/upload'
import { uploadFile } from '../../utils/uploadFile'
import LoadingOutlined from '@ant-design/icons/LoadingOutlined'
import PlusOutlined from '@ant-design/icons/PlusOutlined'

import * as classes from './Profile.module.css'
import TypographyA from 'antd/es/typography'

import message from 'antd/es/message'

import Helmet from 'react-helmet'

import {
    Box,
    createStyles,
    IconButton,
    makeStyles,
    Paper,
    Typography,
    Button,
    Chip,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    CardActionArea,
    CardMedia,
    CardContent,
    DialogContentText,
    TextField,
    Slide,
} from '@material-ui/core'
import Layout from '../../components/Layout/Layout'
import { Edit } from '@material-ui/icons'
import fieldLength from '../../constants/fieldLength'
import Rewards from '../../components/Rewards/Rewards_old'
import moment from 'moment'
import HorizontalScrollList from '../../components/UI/HorizontalScrollList'
import AddProgress from '../Progresses/AddProgress'
import AddFriends from '../Friends/AddFriends'
import DialogFullscreen from '../../components/UI/DialogFullscreen'
import ShareButton from '../../components/UI/ShareButton'
import { sortProgresses } from '../Explore/Explore'
import ProfileRewards from '../../components/ProfileRewards/ProfileRewards'
import { spanStyle } from '../../components/Post/Message'
import Badge from 'antd/es/badge'
import LanguageSwitcher from '../../components/LanguageSwitcher/LanguageSwitcher'
moment.updateLocale('en', {
    relativeTime: {
        future: 'in %s',
        past: '%s',
        s: 'now',
        ss: 'now',
        m: '1 min',
        mm: '%d mins',
        h: '1 hr',
        hh: '%d hrs',
        d: '1 day',
        dd: '%d days',
        w: '1 wk',
        ww: '%d wks',
        M: '1 mth',
        MM: '%d mths',
        y: 'a year',
        yy: '%d years',
    },
})
moment().format()
const TransitionComponent = props => <Slide direction="up" {...props} />

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
        sectionTitle: {
            fontSize: 13,
            margin: '30px 15px 10px',
            textTransform: 'uppercase',
            fontWeight: 600,
        },
    })
)
const Profile = props => {
    const [currentTab, setCurrentTab] = useState('accounts')
    const classesM = useStyles()
    const [data, setData] = useState({})
    const [followersVisible, setFollowersVisible] = useState('')

    const {
        profile,
        account,
        progressData,
        transactionData,
        rewardData,
    } = props.isAuthed ? props : data
    const [deleteVisible, setDeleteVisible] = useState()
    const [deleteField, setDeleteField] = useState('')
    const [startProgressVisible, setStartProgressVisible] = useState(false)
    const [addFriendOpen, setAddFriendOpen] = useState(false)

    useEffect(() => {
        const pathParts = props.history.location.pathname.split('/')
        if (pathParts.length > 2) {
            const profileId = pathParts[2]
            if (props.isAuthed) {
                props.setData({ currentProfile: profileId })
            } else {
                pullData(props, data, setData, { forceAuth: false }, data => {
                    const friendData = {}
                    for (let friend of data.friendData)
                        friendData[friend._id] = friend
                    const progressData = {}
                    for (let progress of data.progressData)
                        progressData[progress._id] = progress

                    const rewardData = {}
                    for (let reward of data.rewardData)
                        rewardData[reward._id] = reward

                    props.setData({
                        profile: data.profile,
                        friendData,
                        progressData,
                        rewardData,
                        account: {},
                    })
                })
            }
        } else {
            if (props.isAuthed) {
                props.setData({ currentProfile: account._id })
            }
        }
    }, [props.isAuthed, props.history.location.pathname, props.account._id])

    if (!profile || !profile._id) return null

    const isOwner = account && account._id && account._id === profile._id
    props.setPageTitle({ main: profile.name, sub: '' })

    const realFriends = [],
        invitedFriends = [],
        invitingFriends = []
    if (profile && profile.friends && account && account._id) {
        for (let friend of account.friends) {
            if (friend.status === 'friend') realFriends.push(friend)
            else if (friend.status === 'invited') invitedFriends.push(friend)
            else if (friend.status === 'inviting') invitingFriends.push(friend)
        }
    }

    const loading = false

    const { progresses, transactions } = profile
    let loadedProgresses
    if (progresses) {
        loadedProgresses = progresses
            .filter(item => progressData[item])
            .map(item => progressData[item])
    }

    // const newTransactions =
    //     (transactions &&
    //         transactions
    //             .filter(item => transactionData[item])
    //             .map(item => transactionData[item])
    //             .filter(item => item.status !== 'Confirmed')) ||
    //     []

    // const recievedTransactions =
    //     (transactions &&
    //         transactions
    //             .filter(item => transactionData[item])
    //             .map(item => transactionData[item])
    //             .filter(item => item.status === 'Confirmed')) ||
    //     []

    return (
        <>
            <Helmet>
                <title>
                    {profile.name} - Addspire - Explore goals and ideas from
                    around the World!
                </title>
                <meta
                    name="description"
                    content="Addspire is a social network where you can inspire and motivate yourself or the people you care."
                />
            </Helmet>
            <Layout
                header={
                    <Typography
                        variant="h6"
                        className={classesM.title}
                        color="inherit"
                        noWrap
                    >
                        {profile.name}
                    </Typography>
                }
            >
                <Paper style={{ padding: '15px' }}>
                    <div style={{ textAlign: 'center', marginTop: '10px' }}>
                        {isOwner ? (
                            <Upload
                                name="avatar"
                                accept="image/*"
                                listType="picture-card"
                                className={classes.Avatar}
                                showUploadList={false}
                                customRequest={({ file }) => {
                                    uploadFile(file, 800, 'avatar', url => {
                                        if (url) props.increaseAvatarV()
                                    })
                                }}
                            >
                                <div
                                    style={{
                                        flex: 1,
                                        display: 'flex',
                                        height: '100%',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                        flexDirection: 'column',
                                    }}
                                    onClick={e => {
                                        if (window.isRNWebView) {
                                            e.stopPropagation()
                                            window.ReactNativeWebView.postMessage(
                                                'uploadFile'
                                            )
                                            window.callback = url => {
                                                props.increaseAvatarV()
                                            }
                                        }
                                    }}
                                >
                                    {account.image ? (
                                        <Avatar
                                            src={getAvatar(
                                                account._id,
                                                account.image
                                            )}
                                            size={150}
                                            style={{ margin: '25px' }}
                                        />
                                    ) : (
                                        <>
                                            {loading ? (
                                                <LoadingOutlined />
                                            ) : (
                                                <PlusOutlined />
                                            )}
                                            <div className="ant-upload-text">
                                                Upload
                                            </div>
                                        </>
                                    )}
                                </div>
                            </Upload>
                        ) : (
                            <Avatar
                                src={getAvatar(profile._id, profile.image)}
                                size={200}
                            />
                        )}

                        <Box style={{ padding: '0 15px' }}>
                            {props.onlineUsers.includes(account._id) && (
                                <Badge color="green" text="online" />
                            )}
                            <TypographyA.Title
                                level={2}
                                editable={
                                    isOwner
                                        ? {
                                              onChange: res =>
                                                  wsActions.editAccount(
                                                      { name: res },
                                                      account._id
                                                  ),
                                              icon: (
                                                  <IconButton color="primary">
                                                      <Edit />
                                                  </IconButton>
                                              ),
                                              maxLength: fieldLength.name,
                                              autoSize: {
                                                  minRows: 1,
                                                  maxRows: 1,
                                              },
                                          }
                                        : false
                                }
                            >
                                {profile.name}
                            </TypographyA.Title>
                        </Box>
                        <Typography variant="h5">({profile._id})</Typography>
                        <Box
                            style={{
                                margin: '5px 0px',
                            }}
                        >
                            <Button
                                onClick={() => setFollowersVisible('followers')}
                                style={{ marginRight: '20px' }}
                            >
                                <span
                                    style={{
                                        textTransform: 'none',
                                    }}
                                >
                                    {profile.followingAccounts.length} Followers
                                </span>
                            </Button>
                            <Button
                                onClick={() => setFollowersVisible('following')}
                            >
                                <span
                                    style={{
                                        textTransform: 'none',
                                    }}
                                >
                                    {profile.followProgresses.length +
                                        profile.followAccounts.length}{' '}
                                    Following
                                </span>
                            </Button>
                        </Box>

                        <div>
                            <ShareButton
                                link={
                                    'https://addspire.com/profile/' +
                                    profile._id
                                }
                                media={
                                    profile.image &&
                                    getAvatar(profile._id, profile.image)
                                }
                                title={
                                    profile.name +
                                    ' account on Addspire.com - a social network for sharing and supporting ideas, promises and resolutions.'
                                }
                            />
                            {account && account._id !== profile._id && (
                                <span style={{ marginRight: '10px' }}>
                                    {realFriends.find(
                                        item => item.friend === profile._id
                                    ) ? (
                                        <Chip color="primary" label="Friends" />
                                    ) : null}
                                    {account.followAccounts &&
                                        !account.followAccounts.includes(
                                            profile._id
                                        ) && (
                                            <Button
                                                color="primary"
                                                onClick={() =>
                                                    wsActions.followAccount(
                                                        account._id,
                                                        profile._id
                                                    )
                                                }
                                                style={{ marginRight: '20px' }}
                                                variant="contained"
                                            >
                                                Follow
                                            </Button>
                                        )}
                                </span>
                            )}
                            <FriendActionButton
                                account={account}
                                realFriends={realFriends}
                                invitedFriends={invitedFriends}
                                invitingFriends={invitingFriends}
                                _id={profile._id}
                                additionalButtons={
                                    isOwner
                                        ? [
                                              {
                                                  onClick: () =>
                                                      props.history.push(
                                                          '/logout'
                                                      ),
                                                  title: 'Logout',
                                              },
                                          ]
                                        : null
                                }
                                asText={true}
                            />
                        </div>
                        <Box />
                    </div>
                </Paper>

                <HorizontalScrollList
                    title="Goals"
                    items={loadedProgresses.sort(sortProgresses)}
                    content={item => (
                        <CardActionArea>
                            <Link to={'/goals/' + item._id}>
                                <CardMedia
                                    style={{
                                        height: '150px',
                                        backgroundSize: 'contain',
                                    }}
                                    image={
                                        item.images.length > 0
                                            ? item.images[0]
                                            : placeholderImages[getPH(item._id)]
                                    }
                                    title={item.name}
                                />
                                <CardContent style={{ padding: '5px' }}>
                                    <Typography variant="body2" noWrap>
                                        {item.name}
                                    </Typography>
                                </CardContent>
                            </Link>
                        </CardActionArea>
                    )}
                    withAdd={isOwner}
                    addTitle="Add goal"
                    onAdd={() => setStartProgressVisible(true)}
                />

                {isOwner && invitedFriends.length > 0 && (
                    <>
                        <HorizontalScrollList
                            title="Pending friendships"
                            items={invitedFriends}
                            content={item => (
                                <CardContent
                                    style={{
                                        textAlign: 'center',
                                        padding: '5px',
                                    }}
                                >
                                    <Box>
                                        <UserButton
                                            noName
                                            id={item.friend}
                                            size={80}
                                        />
                                    </Box>
                                    <UserButton
                                        noAva
                                        typography
                                        style={{
                                            width: '100%',
                                            margin: '3px 0 7px',
                                        }}
                                        id={item.friend}
                                    />
                                    <Box style={{ margin: '5px 0' }}>
                                        <Button
                                            color="primary"
                                            variant="contained"
                                            size="small"
                                            onClick={() =>
                                                wsActions.acceptFriend(
                                                    account._id,
                                                    item.friend
                                                )
                                            }
                                        >
                                            Accept
                                        </Button>
                                    </Box>
                                    <Box>
                                        <Button
                                            size="small"
                                            onClick={() =>
                                                wsActions.unfriend(
                                                    account._id,
                                                    item.friend
                                                )
                                            }
                                        >
                                            Decline
                                        </Button>
                                    </Box>
                                </CardContent>
                            )}
                        />
                    </>
                )}

                {isOwner && invitingFriends.length > 0 && (
                    <>
                        <HorizontalScrollList
                            title="Awaiting approval"
                            items={invitingFriends}
                            content={item => (
                                <CardContent
                                    style={{
                                        textAlign: 'center',
                                        padding: '5px',
                                    }}
                                >
                                    <Box>
                                        <UserButton
                                            noName
                                            id={item.friend}
                                            size={80}
                                        />
                                    </Box>
                                    <UserButton
                                        noAva
                                        typography
                                        style={{
                                            width: '100%',
                                            margin: '3px 0 7px',
                                        }}
                                        id={item.friend}
                                    />
                                    <Box>
                                        <Button
                                            size="small"
                                            onClick={() =>
                                                wsActions.unfriend(
                                                    account._id,
                                                    item.friend
                                                )
                                            }
                                        >
                                            Decline
                                        </Button>
                                    </Box>
                                </CardContent>
                            )}
                        />
                    </>
                )}

                <HorizontalScrollList
                    title="Friends"
                    items={realFriends}
                    content={item => (
                        <CardContent
                            style={{
                                textAlign: 'center',
                                position: 'relative',
                                padding: '5px',
                            }}
                        >
                            <Box>
                                <UserButton noName id={item.friend} size={80} />
                            </Box>
                            <UserButton
                                noAva
                                typography
                                style={{
                                    width: '100%',
                                    margin: '3px 0 7px',
                                }}
                                id={item.friend}
                            />

                            <FriendActionButton
                                account={account}
                                realFriends={realFriends}
                                invitedFriends={invitedFriends}
                                invitingFriends={invitingFriends}
                                _id={item.friend}
                                asIcons={true}
                                minimized
                            />
                        </CardContent>
                    )}
                    withAdd={isOwner}
                    addTitle="Add friend"
                    onAdd={() => setAddFriendOpen(true)}
                />

                <HorizontalScrollList
                    title="Recieved and given rewards"
                    items={transactions
                        .map(item => transactionData[item])
                        .filter(item => item)
                        .filter(item => rewardData[item.reward])
                        .map(item => ({
                            ...item,
                            rewardObject: rewardData[item.reward],
                        }))
                        .sort((a, b) => {
                            if (
                                a.status === 'Confirmed' &&
                                b.status !== 'Confirmed'
                            )
                                return 1
                            if (
                                a.status !== 'Confirmed' &&
                                b.status === 'Confirmed'
                            )
                                return -1
                            if (
                                a.status !== 'Confirmed' &&
                                b.status !== 'Confirmed'
                            ) {
                                if (
                                    a.to === profile._id &&
                                    b.to !== profile._id
                                )
                                    return 1
                                if (
                                    a.to !== profile._id &&
                                    b.to === profile._id
                                )
                                    return -1
                            }
                            return new Date(a.date).getTime() <
                                new Date(b.date).getTime()
                                ? 1
                                : -1
                        })}
                    content={item => (
                        <CardContent
                            style={{
                                textAlign: 'center',
                                position: 'relative',
                                padding: '5px',
                            }}
                        >
                            <Link to={'/rewards/' + item.rewardObject._id}>
                                <CardMedia
                                    style={{
                                        height: '150px',
                                        backgroundSize: 'contain',
                                    }}
                                    image={
                                        item.rewardObject.images.length > 0
                                            ? item.rewardObject.images[0]
                                            : placeholderImages[getPH(item._id)]
                                    }
                                    title={item.rewardObject.name}
                                />
                                <CardContent
                                    style={{
                                        padding: '5px',
                                    }}
                                >
                                    <Typography variant="body2" noWrap>
                                        {item.rewardObject.name}
                                    </Typography>
                                    <span style={spanStyle}>
                                        {moment(item.date).fromNow()}
                                    </span>
                                </CardContent>
                            </Link>
                            <UserButton
                                noName
                                id={item.rewardObject.from}
                                size={25}
                            />
                            {'>'}
                            <UserButton
                                noName
                                id={item.rewardObject.to}
                                size={25}
                            />
                            {item.status !== 'Confirmed' && (
                                <Button
                                    color="primary"
                                    variant="contained"
                                    onClick={() =>
                                        wsActions.confirmTransaction(
                                            account._id,
                                            item._id
                                        )
                                    }
                                >
                                    {item.to === profile._id
                                        ? 'Received'
                                        : 'Gave'}
                                </Button>
                            )}
                        </CardContent>
                    )}
                />

                <ProfileRewards owner={profile} />
                <ProfileRewards owner={profile} wish />

                {isOwner && (
                    <>
                        <Typography
                            className={classesM.sectionTitle}
                            color="textSecondary"
                            gutterBottom
                        >
                            Settings
                        </Typography>
                        <Paper
                            style={{
                                padding: '15px',
                            }}
                        >
                            <Box>
                                <Button
                                    onClick={() =>
                                        props.history.push('/logout')
                                    }
                                    color="secondary"
                                >
                                    Logout
                                </Button>
                            </Box>
                            <LanguageSwitcher settings />
                            <Button
                                onClick={() => setDeleteVisible(true)}
                                color="secondary"
                            >
                                Delete account
                            </Button>
                        </Paper>

                        <Dialog open={Boolean(deleteVisible)}>
                            <DialogTitle>{'Delete account'}</DialogTitle>
                            <DialogContent>
                                <DialogContentText>
                                    <p>
                                        Please enter your nickname (
                                        {account._id}) and press Ok to delete
                                        your account.
                                    </p>
                                    <TextField
                                        onChange={e =>
                                            setDeleteField(e.target.value)
                                        }
                                    />
                                </DialogContentText>
                            </DialogContent>
                            <DialogActions>
                                <Button onClick={() => setDeleteVisible(false)}>
                                    Cancel
                                </Button>
                                <Button
                                    onClick={() => {
                                        if (deleteField !== account._id) {
                                            message.warning(
                                                'The nickmanme is not correct.'
                                            )
                                        } else {
                                            wsActions.deleteAccount(account._id)
                                        }
                                    }}
                                    color="primary"
                                >
                                    Delete
                                </Button>
                            </DialogActions>
                        </Dialog>
                    </>
                )}
            </Layout>
            <Dialog
                open={Boolean(followersVisible)}
                onClose={() => setFollowersVisible('')}
                title={
                    followersVisible === 'following' ? 'Following' : 'Followers'
                }
                fullWidth
                maxWidth="md"
            >
                <DialogTitle>
                    {followersVisible !== 'following' && 'Followers'}
                    {followersVisible === 'following' && (
                        <>
                            <Button
                                style={{ margin: '0px 20px 0px 0px' }}
                                color={
                                    currentTab === 'accounts'
                                        ? 'primary'
                                        : 'default'
                                }
                                onClick={() => setCurrentTab('accounts')}
                                variant="contained"
                            >
                                Accounts
                            </Button>
                            <Button
                                color={
                                    currentTab === 'goals'
                                        ? 'primary'
                                        : 'default'
                                }
                                onClick={() => setCurrentTab('goals')}
                                variant="contained"
                            >
                                Goals
                            </Button>
                        </>
                    )}
                </DialogTitle>
                <DialogContent>
                    {followersVisible === 'following' ? (
                        currentTab === 'accounts' ? (
                            profile.followAccounts.map(item => (
                                <MasonryCard
                                    friends={true}
                                    noLink={true}
                                    key={item}
                                    actions={
                                        <FollowButton
                                            account={account}
                                            id={item}
                                        />
                                    }
                                    body={
                                        <UserButton
                                            span={true}
                                            id={item}
                                            size={50}
                                        />
                                    }
                                />
                            ))
                        ) : (
                            <MasonryGrid>
                                {profile.followProgresses.map(item => (
                                    <MasonryCard
                                        id={item}
                                        onClickLink={'/goals/' + item}
                                        // noLink
                                        key={item}
                                        actions={
                                            <FollowButton
                                                type="progress"
                                                account={account}
                                                id={item}
                                            />
                                        }
                                        body={
                                            <ProgressButton
                                                span={true}
                                                id={item}
                                                size={50}
                                            />
                                        }
                                        style={{
                                            // maxHeight: '170px',
                                            marginTop: '20px',
                                        }}
                                    />
                                ))}
                            </MasonryGrid>
                        )
                    ) : (
                        profile.followingAccounts.map(item => (
                            <MasonryCard
                                friends={true}
                                noLink={true}
                                key={item}
                                actions={
                                    <FollowButton account={account} id={item} />
                                }
                                body={
                                    <UserButton
                                        span={true}
                                        id={item}
                                        size={50}
                                    />
                                }
                            />
                        ))
                    )}
                </DialogContent>
                <DialogActions>
                    <Button
                        onClick={() => setFollowersVisible('')}
                        color="primary"
                    >
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
            {isOwner && (
                <Dialog
                    fullScreen
                    open={startProgressVisible}
                    onClose={() => {
                        setStartProgressVisible(false)
                    }}
                    TransitionComponent={TransitionComponent}
                >
                    <AddProgress
                        setStartProgressVisible={setStartProgressVisible}
                        account={account}
                        friendData={props.friendData}
                        cancel={() => setStartProgressVisible(false)}
                    />
                </Dialog>
            )}
            {isOwner && (
                <DialogFullscreen
                    open={addFriendOpen}
                    close={() => {
                        setAddFriendOpen(false)
                    }}
                    title="Add friend"
                >
                    <AddFriends
                        close={() => setAddFriendOpen(false)}
                        open={addFriendOpen}
                        account={account}
                        friendData={props.friendData}
                    />
                </DialogFullscreen>
            )}
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        profile: state.profile,
        isAuthed: state.isAuthed,
        friendData: state.friendData,
        progressData: state.progressData,
        transactionData: state.transactionData,
        rewardData: state.rewardData,
        onlineUsers: state.onlineUsers,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        tryAuth: (props, forceAuth) =>
            dispatch(actions.tryAuth(props, forceAuth)),
        increaseAvatarV: () => dispatch(actions.increaseAvatarV()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Profile)
)
