import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

const ProgressButton = props => {
    const { id } = props
    if (!props.progressData[id]) return null
    const userName = id => props.progressData[id].name

    const handleClick = id => props.history.push('/goals/' + (id || props.id))
    return (
        <span
            style={{ cursor: 'pointer', fontWeight: 'bold', ...props.style }}
            onClick={e => {
                e.stopPropagation()
                handleClick()
            }}
        >
            {userName(id)}
        </span>
    )
}

const mapStateToProps = state => {
    return {
        progressData: state.progressData,
    }
}

export default withRouter(connect(mapStateToProps)(ProgressButton))
