import React, { useRef, useState, useEffect } from 'react'

import Form from 'antd/es/form'

import Rewards from '../Rewards/Rewards'
import { nameRules, descriptionRules } from '../../constants/fieldRules'
import ConnectionsSelect from './ConnectionsSelect'
import Select from 'antd/es/select'
import { Map, TileLayer } from 'react-leaflet'
import 'leaflet/dist/leaflet.css'

import * as ELG from 'esri-leaflet-geocoder'
import 'esri-leaflet-geocoder/dist/esri-leaflet-geocoder.css'
import LocateControl from '../../containers/Explore/LocateControl'
import { GestureHandling } from 'leaflet-gesture-handling'
import 'leaflet-gesture-handling/dist/leaflet-gesture-handling.css'
import ImagePickerMulti from '../UI/ImagePickerMulti'
import {
    AppBar,
    createStyles,
    IconButton,
    makeStyles,
    Theme,
    Toolbar,
    Typography,
    Button,
    Input,
} from '@material-ui/core'
import { Close } from '@material-ui/icons'
import TinyEditor from '../UI/TinyEditor'
import htmlToText from 'html-to-text'
const { Option } = Select

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            padding: '2px 4px',
            display: 'flex',
            alignItems: 'center',
            maxWidth: 400,
            width: '100%',
        },
        input: {
            marginLeft: theme.spacing(1),
            flex: 1,
        },
        iconButton: {
            padding: 10,
        },
        divider: {
            height: 28,
            margin: 4,
        },
        appBar: {
            position: 'fixed',
        },
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
    })
)
const EditGoalAndProgressForm = props => {
    const map = useRef(null)
    useEffect(() => {
        if (map && map.current && map.current.leafletElement) {
            new ELG.Geosearch().addTo(map.current.leafletElement)
            new GestureHandling(map.current.leafletElement)
        }
    }, [])
    const { progress, account, friendData } = props

    const defPos = [40.689078, -74.044526]
    const [position, setPosition] = useState(
        progress && progress.position && progress.position.coordinates
            ? [
                  progress.position.coordinates[1],
                  progress.position.coordinates[0],
              ]
            : defPos
    )

    const [zoom, setZoom] = useState(10)
    const [descriptionPlain, setDescriptionPlain] = useState(
        progress && progress.descriptionText
    )

    const onFinish = value => {
        props.onFinish({
            ...value,
            descriptionText: descriptionPlain,
            position: {
                type: 'Point',
                coordinates: [position[1], position[0]],
            },
        })
    }

    const layout = {
        labelCol: { span: 4 },
        wrapperCol: { span: 20 },
    }

    const [form] = Form.useForm()

    const mapDragEnd = e => {
        setZoom(e.target._zoom)

        setPosition([e.target.getCenter().lat, e.target.getCenter().lng])
        form.setFieldsValue({ nomap: false })
    }
    const classes = useStyles()

    return !props.goalId || progress ? (
        <>
            <AppBar className={classes.appBar} color="primary">
                <Toolbar>
                    <IconButton
                        edge="start"
                        color="inherit"
                        onClick={props.cancel}
                        aria-label="close"
                    >
                        <Close />
                    </IconButton>
                    <Typography
                        variant="h6"
                        className={classes.title}
                        color="inherit"
                    >
                        Add new goal
                    </Typography>
                    <Button
                        autoFocus
                        color="inherit"
                        onClick={() => form.submit()}
                    >
                        Save
                    </Button>
                </Toolbar>
            </AppBar>
            <Toolbar />

            <Form
                style={{
                    maxWidth: '1200px',
                    margin: 'auto',
                    padding: '15px',
                    width: '100%',
                }}
                form={form}
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                initialValues={
                    progress || {
                        name: 'New achievement',
                        repeat: 'no',
                        days: ['0'],
                        nomap: false,
                    }
                }
                {...layout}
                onValuesChange={(changedFields, allValues) => {
                    allValues.days &&
                        form.setFieldsValue({
                            days: [...allValues.days].sort(),
                        })
                }}
            >
                <Form.Item
                    name="images"
                    label="Images"
                    valuePropName="images"
                    trigger="onValueChange"
                >
                    <ImagePickerMulti />
                </Form.Item>
                <Form.Item
                    label="Name"
                    name="name"
                    rules={nameRules}
                    getValueFromEvent={event => event.target.value}
                >
                    <Input fullWidth={true} />
                </Form.Item>
                <Form.Item
                    label="Description"
                    name="description"
                    // rules={descriptionRules}
                    trigger="onEditorChange"
                >
                    <TinyEditor
                        onEditorChangePlain={value =>
                            setDescriptionPlain(value)
                        }
                    />
                </Form.Item>
                <Form.Item label="Achievers" name="users">
                    <ConnectionsSelect
                        account={account}
                        friendData={friendData}
                    />
                </Form.Item>

                <Form.Item label="Repeat" name="repeat">
                    <Select getPopupContainer={trigger => trigger.parentNode}>
                        <Option value="no">No repeat</Option>
                        <Option value="day">Every day</Option>
                        <Option value="weekday">Every day of week</Option>
                        <Option value="week">Every week</Option>
                        <Option value="month">Every month</Option>
                        <Option value="year">Every year</Option>
                    </Select>
                </Form.Item>
                <Form.Item
                    noStyle
                    shouldUpdate={(prevValues, currentValues) =>
                        prevValues.repeat !== currentValues.repeat
                    }
                >
                    {({ getFieldValue }) => {
                        return getFieldValue('repeat') === 'weekday' ? (
                            <Form.Item label="Days of week" name="days">
                                <Select
                                    mode="tags"
                                    getPopupContainer={trigger =>
                                        trigger.parentNode
                                    }
                                >
                                    <Option value="0">Sunday</Option>
                                    <Option value="1">Monday</Option>
                                    <Option value="2">Tuesday</Option>
                                    <Option value="3">Wednesday</Option>
                                    <Option value="4">Thursday</Option>
                                    <Option value="5">Friday</Option>
                                    <Option value="6">Saturday</Option>
                                </Select>
                            </Form.Item>
                        ) : null
                    }}
                </Form.Item>
                <Form.Item
                    name="rewards"
                    label="Rewards"
                    valuePropName="rewards"
                    trigger="onValueChange"
                >
                    <Rewards />
                </Form.Item>
                <Form.Item label="Place">
                    <div
                        style={{
                            position: 'relative',
                            height: '300px',
                        }}
                    >
                        <Map
                            ref={map}
                            center={position}
                            zoom={zoom}
                            ondragend={mapDragEnd}
                            onmoveend={mapDragEnd}
                            style={{
                                width: '100%',
                                height: '100%',
                                position: 'relative',
                                zIndex: 1,
                            }}
                            gestureHandling={true}
                        >
                            <TileLayer
                                attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                            />
                            <LocateControl />
                        </Map>
                        <div
                            style={{
                                backgroundImage: `url(${require('../../assets/images/pin.png')})`,
                                width: '20px',
                                height: '35px',
                                position: 'absolute',
                                zIndex: 2,
                                left: 'calc(50% - 10px)',
                                top: 'calc(50% - 35px)',
                                pointerEvents: 'none',
                                touchAction: 'none',
                            }}
                        />
                    </div>
                </Form.Item>
                <div style={{ textAlign: 'right', marginTop: '50px' }}>
                    <Button size="large" onClick={() => props.cancel(false)}>
                        Cancel
                    </Button>
                    <Button
                        color="primary"
                        variant="contained"
                        type="submit"
                        size="large"
                        style={{
                            marginLeft: '10px',
                            marginRight: '50px',
                        }}
                    >
                        Save
                    </Button>
                </div>
            </Form>
        </>
    ) : null
}
export default EditGoalAndProgressForm
