import React, { useState } from 'react'
import { connect } from 'react-redux'
import { Link, withRouter } from 'react-router-dom'
import HorizontalScrollList from '../UI/HorizontalScrollList'
import * as actions from '../../store/actions'
import * as wsActions from '../../websocketActions'
import {
    Button,
    CardContent,
    CardMedia,
    Dialog,
    DialogActions,
    DialogTitle,
    IconButton,
    Menu,
    MenuItem,
    Typography,
} from '@material-ui/core'
import { MoreVertOutlined } from '@material-ui/icons'
import { getPH, placeholderImages } from '../MasonryCard'
import ProfileRewards from '../ProfileRewards/ProfileRewards'
import DialogFullscreen from '../UI/DialogFullscreen'

const Rewards = props => {
    // const [anchorEl, setAnchorEl] = useState(null)
    const [addRewardOpen, setAddRewardOpen] = useState(false)
    const [deleteRewardVisible, setDeleteRewardVisible] = useState(false)

    const { rewardData, account, progress, rewards, onValueChange } = props

    // const handleClose = () => {
    //     setAnchorEl(null)
    // }
    // const handleClick = event => {
    //     setAnchorEl(event.currentTarget)
    // }

    const rewardsIds = (onValueChange ? rewards : progress.rewards) || []
    return progress || onValueChange ? (
        <>
            <HorizontalScrollList
                title="Rewards"
                items={rewardsIds
                    .map(item => rewardData[item])
                    .filter(item => item)}
                content={item => (
                    <CardContent
                        style={{
                            textAlign: 'center',
                            position: 'relative',
                            padding: '5px',
                        }}
                    >
                        <Link to={'/rewards/' + item._id}>
                            <CardMedia
                                style={{
                                    height: '150px',
                                    backgroundSize: 'contain',
                                }}
                                image={
                                    item.images.length > 0
                                        ? item.images[0]
                                        : placeholderImages[getPH(item._id)]
                                }
                                title={item.name}
                            />
                            <CardContent style={{ padding: '5px' }}>
                                <Typography variant="body2" noWrap>
                                    {item.name}
                                </Typography>
                            </CardContent>
                        </Link>
                        {(account._id === item.owner ||
                            account._id === progress.owner ||
                            rewards) && (
                            <Button
                                onClick={() => {
                                    setDeleteRewardVisible(item._id)
                                }}
                            >
                                Remove
                            </Button>
                            // <>
                            //     <IconButton onClick={e => handleClick(e)}>
                            //         <Delete />
                            //     </IconButton>
                            //     <Menu
                            //         anchorEl={anchorEl}
                            //         open={Boolean(anchorEl)}
                            //         onClose={handleClose}
                            //     >
                            //         <MenuItem
                            //             onClick={() => {
                            //                 setDeleteRewardVisible(item._id)
                            //                 handleClose()
                            //             }}
                            //         >
                            //             Delete
                            //         </MenuItem>
                            //     </Menu>
                            // </>
                        )}
                    </CardContent>
                )}
                withAdd={Boolean(account._id)}
                addTitle="Add reward"
                onAdd={() => setAddRewardOpen(true)}
            />
            <DialogFullscreen
                open={addRewardOpen}
                close={() => setAddRewardOpen(false)}
                title="Add reward to goal"
            >
                <ProfileRewards
                    owner={account}
                    openInDialog
                    additionalButton={item => (
                        <Button
                            color="primary"
                            onClick={() => {
                                if (onValueChange) {
                                    onValueChange([...rewardsIds, item._id])
                                } else
                                    wsActions.addRewardToProgress(
                                        account._id,
                                        progress._id,
                                        item._id
                                    )
                                setAddRewardOpen(false)
                            }}
                        >
                            Choose
                        </Button>
                    )}
                />
            </DialogFullscreen>
            <Dialog
                open={Boolean(deleteRewardVisible)}
                onClose={() => setDeleteRewardVisible(false)}
            >
                <DialogTitle>
                    Are youe sure you want to delete this reward? This reward
                    will be only deleted from this goal.
                </DialogTitle>

                <DialogActions>
                    <Button
                        onClick={() => setDeleteRewardVisible(false)}
                        color="primary"
                    >
                        Disagree
                    </Button>
                    <Button
                        onClick={() => {
                            if (onValueChange) {
                                let newRewards = [...rewardsIds]
                                newRewards.splice(
                                    newRewards.indexOf(deleteRewardVisible),
                                    1
                                )
                                onValueChange(newRewards)
                            } else
                                wsActions.deleteRewardFromProgress(
                                    account._id,
                                    progress._id,
                                    deleteRewardVisible
                                )

                            setDeleteRewardVisible(false)
                        }}
                        color="primary"
                    >
                        Agree
                    </Button>
                </DialogActions>
            </Dialog>
        </>
    ) : null
}

const mapStateToProps = state => {
    return {
        account: state.account,
        friendData: state.friendData,
        rewardData: state.rewardData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Rewards)
)
